.app {}

.header .logo {
	position: absolute;
	top: 30px;
	right: 20px;
}

.header {
	background-color: rgb(196, 191, 191);
	height: 130px;
	width: 100%;
	padding: 0 120px;
	font-size: 5em;
	border-bottom: 4px solid black;
	line-height: 130px;
}

.section {
	padding: 40px;
	text-align: center;
	display: grid;
	justify-items: left;
	align-items: center;
}

.section-title {
	font-size: 4.5em;
	display: grid;
	grid-template-columns: 120px auto;
	align-items: center;
	justify-items: center;
	margin-left: 40px;
	text-align: center;
	white-space: nowrap;
	column-gap: 50px;
}

.home .menu {
	position: absolute;
	bottom: 150px;
	text-align: center;
	width: 100%;
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-items: center;
	align-items: center;
	font-size: 4em;
}

.home .menu div {
	/* border: 4px solid black; */
	padding: 40px;
	padding-bottom: 0;
	width: 600px;
	height: 200px;
}

.home .section .new-settings {
	padding-top: 80px;
	font-size: 2em;
	margin-left: 120px;
}

.home .section .select-existing-settings {
	width: 100%;
}

.home .section .select-existing-settings input {
	font-size: 2.5em;
	height: 70px;
	margin-left: 120px;
	float: left;
	margin-top: 30px;
	background-color: white;
	border: 3px solid grey;
	padding: 10px;
	width: 600px;
}

.home .section .select-existing-settings ul {
	font-size: 2.5em;
	position: absolute;
	height: 70px;
	background-color: white;
	border: 3px solid grey;
	padding: 10px;
	width: 600px;
	height: 390px;
	left: 160px;
	top: 160px;
	overflow-y: scroll;
	list-style-type: none;
}

.home .section .select-existing-settings li {
	text-align: left;
}

.new-settings .steps {
	display: grid;
	width: 60%;
	grid-template-columns: repeat(5, 1fr);
	align-items: center;
	justify-items: center;
	font-size: 2.5em;
	margin-left: 100px;
}

.new-settings .steps div {
	text-align: center;
}

.select-specimen {
	display: grid;
	width: 100%;
	grid-template-columns: 1fr 1fr;
	font-size: 1.8em;
	padding: 50px 150px;
}

datalist {
	box-sizing: content-box;
}

.new-settings select {
	font-size: 1em;
	height: 60px;
	background-color: white;
	border: 3px solid grey;
	padding: 10px;
	margin-top: 20px;
	width: 700px;
}

.new-settings .set-dimensions img {
	margin-left: 100px;
}

.new-settings input {
	position: absolute;
	text-align: center;
	font-size: 2em;
	border: 1px solid black;
	padding: 10px;
	width: 70px;
}

.new-settings .input1 {
	left: 65px;
	bottom: 340px;
}

.new-settings .input2 {
	left: 670px;
	bottom: 550px;
}

.new-settings .input3 {
	left: 370px;
	bottom: 150px;
}

.new-settings .input4 {
	left: 950px;
	bottom: 330px;
}

.new-settings .input5 {
	left: 950px;
	bottom: 150px;
}

a img {
	display: block;
	margin: auto;
}