.keyboard {
	width: 100%;
	max-width: 1030px;
	margin: 0 auto;
	background: #dadada;
	box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.5)
}

.keyboard-wrapper {
	position: fixed;
	top: 200px;
	right: 400px;
	z-index: 10000;
	width: 700px;
}

.keyboard-wrapper .keyboard-row {
	display: flex
}

.keyboard-button {
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-basis: 100px;
	font-size: 18px;
	height: 60px;
	border-radius: 4px;
	background-color: #F5F5F5;
	border: 1px solid #CECECE;
	font-family: 'Roboto', sans-serif;
	font-weight: 300
}

.keyboard-button:focus {
	outline: none
}

.keyboard-button:disabled {
	opacity: 0.4;
	cursor: default
}

.keyboard-button:disabled {
	opacity: 0.4;
	cursor: default
}

.keyboard-button:active {
	background-color: #cccccc
}

.keyboard-button.shift-symbols {
	flex-basis: 210px
}

.keyboard-button.keyboard-numberButton {
	flex-grow: 1
}

.keyboard-button.keyboard-halfButton {
	flex-basis: 56px
}

.keyboard-button.keyboard-space {
	flex-grow: 1
}

.keyboard-button.keyboard-utilButton {
	flex-grow: 2;
	max-width: 55px
}

.keyboard-button.keyboard-additionalButton {
	flex-basis: 128px
}

.keyboard-button.keyboard-submitButton, .keyboard-button.keyboard-submit-button {
	flex-basis: 95px;
	background-color: #63b324;
	border-color: #63b324;
	color: #ffffff
}

.keyboard-keysSet {
	display: flex;
	flex-grow: 1
}

